body {
  color: #757e63;
  background-color: #f5f5f5;
  font-family: helvetica neue;
}

@media (width <= 768px) {
  .mobile {
    display: block;
  }

  .desktop {
    display: none;
  }

  .mobile-container {
    grid-template: "a"
                   "b"
                   "c" 1fr
                   / 100%;
    min-height: 98vh;
    display: grid;
  }

  .header {
    flex-direction: row;
    justify-content: end;
    align-items: center;
    margin-bottom: 20px;
    font-size: 30px;
    font-weight: 200;
    display: flex;
  }

  .bda-logo {
    align-items: center;
    display: flex;
  }

  .menu-image {
    width: 50px;
    height: 50px;
  }

  .bda-title {
    text-align: center;
    flex-grow: 1;
  }

  .bda-hamburger {
    justify-content: flex-end;
    margin-left: auto;
  }

  .mobile-main {
    margin-top: 20px;
  }

  .mob-a {
    grid-area: a;
  }

  .mobile-main-title {
    text-align: right;
    font-size: 20px;
    font-weight: 200;
  }

  .mobile-sub-title {
    text-align: right;
    font-size: 15px;
    font-weight: 200;
  }

  .mob-b {
    grid-area: b;
  }

  .mob-c {
    grid-area: c;
  }
}

@media (width >= 769px) {
  .mobile {
    display: none;
  }

  .desktop {
    display: block;
  }

  .desktop-container {
    grid-template: "b c c c c" 1fr
    / 20% 20% 20% 20% 20%;
    min-height: 98vh;
    display: grid;
  }

  .header {
    flex-direction: row;
    align-items: center;
    margin-bottom: 20px;
    font-size: 25px;
    font-weight: 200;
    display: flex;
  }

  .desk-b {
    grid-area: b;
  }

  .desk-c {
    grid-area: c;
  }

  nav {
    display: none;
  }

  .menu-container {
    flex-direction: column;
    align-items: center;
    margin-left: 5%;
    display: flex;
  }

  #menu-box {
    text-align: center;
    margin-top: 30px;
    margin-bottom: 15px;
  }

  .menu-image {
    width: 100px;
    height: 100px;
  }

  .page-header {
    flex-direction: column;
    align-items: flex-end;
    display: flex;
  }

  h1 {
    margin-bottom: 0%;
    font-size: 50px;
    font-weight: 200;
  }

  h2 {
    margin-top: 0%;
    font-weight: 200;
  }

  ul {
    text-align: left;
    margin: 0;
    padding: 0;
    list-style-type: none;
  }

  ul li {
    margin-bottom: 15px;
    font-size: 20px;
    font-weight: 200;
  }

  li:hover {
    color: #1d1b1b;
  }

  li:first-child {
    margin-bottom: 30px;
  }

  a {
    color: inherit;
    text-decoration: none;
  }

  #current {
    text-decoration-line: underline;
    text-decoration-thickness: 1px;
  }

  #top {
    font-size: 30px;
  }
}
